@import url('https://fonts.googleapis.com/css?family=Raleway:400,500');

@media only screen and (max-width: 600px) {
  body {
    font-family: 'Raleway', sans-serif;
    font-size: 1.75em;
    flex-direction: column-reverse;

    #new-quote{
      margin-bottom: 10px;
    }

  }
}

#text {
  width: 550px;
  font-weight: 500;
  font-size: 1.5em;
  text-align: center;


}

#quote-symb {
  font-size: 1em;
  margin-right: 0.7em;
}


@-webkit-keyframes fade {
  0%   { opacity: 0; }
  100% { opacity: 1; }
}
@-moz-keyframes fade {
  0%   { opacity: 0; }
  100% { opacity: 1; }
}
@-o-keyframes fade {
  0%   { opacity: 0; }
  100% { opacity: 1; }
}
@keyframes fade {
  0%   { opacity: 0; }
  100% { opacity: 1; }
}




.App {

  text-align: center;
  animation-name: fade;
  animation-duration: 2s; 
  animation-fill-mode: forwards; 

  .App-header {
    background-color: #282c34;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
  }


  .buttons{

    display: flex;
    justify-content: space-between;
    min-height: 50px;
    align-items: center;
    height: 100%;
    width: 100%;


    #tweet-quote{
    float: left;
    padding: 10px;
    color: white;
    text-decoration: none;
    cursor: pointer;
    border-radius: 9px;
    box-shadow: 3px 3px #888888;
    }
    #new-quote{
      float: right;
      color: white;
      border-radius: 9px;
      padding: 10px;
      border: none;
      cursor: pointer;
      box-shadow: 3px 3px #888888;
    }
  }

.buttonEffect:hover {background-color: #3e8e41}

.buttonEffect:active {
  background-color: #3e8e41;
  box-shadow: 0 5px #666;
  transform: translateY(4px);
}




  #quote-box {

    padding: 25px;

    background-color: white;
    border-radius: 10px;
    color: black;
    #author{
      text-align: end;
    }
  }
  
  
  
}


